========================================================================================== -->


<template>
  <div>
    <vx-card title="Images" class="carousel-example-gallery" code-toggler>
      <div>
        <!-- swiper1 -->
        <swiper
          :options="swiperOptionTop"
          class="gallery-top"
          ref="swiperTop"
          :dir="$vs.rtl ? 'rtl' : 'ltr'"
          :key="$vs.rtl + ' gallery'"
        >
          <swiper-slide>
            <img
              class="responsive"
              src="@/assets/images/resume/UAT.jpg"
              alt="banner"
            />
          </swiper-slide>
          <swiper-slide>
            <img
              class="responsive"
              src="@/assets/images/resume/uatAsset.jpg"
              alt="banner"
            />
          </swiper-slide>
          <swiper-slide>
            <img
              class="responsive"
              src="@/assets/images/resume/stock.jpg"
              alt="banner"
            />
          </swiper-slide>
          <swiper-slide>
            <img
              class="responsive"
              src="@/assets/images/resume/manageUser.jpg"
              alt="banner"
            />
          </swiper-slide>
          <div
            class="swiper-button-next swiper-button-white"
            slot="button-next"
          ></div>
          <div
            class="swiper-button-prev swiper-button-white"
            slot="button-prev"
          ></div>
        </swiper>

        <!-- swiper2 Thumbs -->
        <swiper
          :options="swiperOptionThumbs"
          class="gallery-thumbs"
          ref="swiperThumbs"
          :dir="$vs.rtl ? 'rtl gallery' : 'ltr gallery'"
          :key="$vs.rtl + ' thumbs'"
        >
          <swiper-slide>
            <img
              class="responsive"
              src="@/assets/images/resume/UAT.jpg"
              alt="banner"
            />
          </swiper-slide>
          <swiper-slide>
            <img
              class="responsive"
              src="@/assets/images/resume/uatAsset.jpg"
              alt="banner"
            />
          </swiper-slide>
          <swiper-slide>
            <img
              class="responsive"
              src="@/assets/images/resume/stock.jpg"
              alt="banner"
            />
          </swiper-slide>
          <swiper-slide>
            <img
              class="responsive"
              src="@/assets/images/resume/manageUser.jpg"
              alt="banner"
            />
          </swiper-slide>
        </swiper>
      </div>
    </vx-card>
    <vx-card>
      <h5 class="mb-2">Description</h5>
      <p class="text-black mb-2">
        This is a webapp games management produced by me designed and done from
        start to finish.
      </p>

      <h5 class="mb-2">Technology</h5>

      <p class="text-black mb-2">
        I used Vuejs for front end and Nodejs connect Firebase-admin for
        backend.
      </p>

      <h5 class="mb-2">Github</h5>

      <p class="text-black mb-2">
        Server: https://github.com/Vietc3/NODEJ-ADVANCED/tree/master/server
        Client: https://github.com/Vietc3/NODEJ-ADVANCED/tree/master/client
      </p>
    </vx-card>
  </div>
</template>

<script>
import "swiper/dist/css/swiper.min.css";
import { swiper, swiperSlide } from "vue-awesome-swiper";

export default {
  data() {
    return {
      swiperOptionTop: {
        spaceBetween: 10,
        navigation: {
          nextEl: ".swiper-button-next",
          prevEl: ".swiper-button-prev",
        },
      },
      swiperOptionThumbs: {
        spaceBetween: 10,
        centeredSlides: true,
        slidesPerView: "auto",
        touchRatio: 0.2,
        slideToClickedSlide: true,
      },
    };
  },
  mounted() {
    this.$nextTick(() => {
      const swiperTop = this.$refs.swiperTop.swiper;
      const swiperThumbs = this.$refs.swiperThumbs.swiper;
      swiperTop.controller.control = swiperThumbs;
      swiperThumbs.controller.control = swiperTop;
    });
  },
  components: {
    swiper,
    swiperSlide,
  },
};
</script>


<style lang="scss" scoped>
.swiper-container {
  background-color: #000;
}
.gallery-top {
  height: 80% !important;
  width: 100%;
}
.gallery-thumbs {
  height: 20% !important;
  box-sizing: border-box;
  padding: 10px 0;
}
.gallery-thumbs .swiper-slide {
  width: 25%;
  height: 100%;
  opacity: 0.4;
}
.gallery-thumbs .swiper-slide-active {
  opacity: 1;
}
</style>
